.chatMessage {
    width: 95%;
    white-space: pre-line;
}

.role-assistant {
    background-color: #dff0ff;
}

.role-user {
    background-color: #f9fff2;
}

.role-error {
    background-color: #f2dede;
}

.chatEntry {
    border: 1px solid #ccc;
    border-radius: 8px;
    margin: 3px;
    overflow: hidden;
}

.chatAvatar {
    width: 80px;
    background-color: #f9f9f9;
    padding: 10px;
    border-right: 1px solid #ccc;
}

.chatEntry textarea {
    background-color: #fff0;
    border: none;
    padding: 0;
    min-height: 0;
}